import React, { createContext, useState, useEffect, useContext } from 'react';

// Defina a interface para o usurio logado (corresponda ao seu backend)
interface LoggedInUser {
    id: number;
    nome: string;
    email: string;
    // Adicione outros campos conforme necessrio
}

export interface AuthContextType {
    loggedInUser: LoggedInUser | null;
    login: (user: LoggedInUser) => void;
    logout: () => void;
}

// Crie o contexto de autenticao com um valor padro (undefined)
const AuthContext = createContext<AuthContextType | undefined>(undefined);

// Crie o Provider do contexto
export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const [loggedInUser, setLoggedInUser] = useState<LoggedInUser | null>(null);

    useEffect(() => {
        // Ao montar o Provider, tente carregar o usurio logado do localStorage
        const storedUser = localStorage.getItem('loggedInUser');
        if (storedUser) {
            try {
                setLoggedInUser(JSON.parse(storedUser));
            } catch (error) {
                console.error('Erro ao analisar usurio do localStorage:', error);
                localStorage.removeItem('loggedInUser'); // Limpar se houver um erro
            }
        }
    }, []);

    // Funo para fazer login do usurio e atualizar o estado e o localStorage
    const login = (user: LoggedInUser) => {
        setLoggedInUser(user);
        localStorage.setItem('loggedInUser', JSON.stringify(user));
    };

    // Funo para fazer logout do usurio e limpar o estado e o localStorage
    const logout = () => {
        setLoggedInUser(null);
        localStorage.removeItem('loggedInUser');
    };

    // Fornea o estado e as funes atravs do contexto
    return (
        <AuthContext.Provider value={{ loggedInUser, login, logout }}>
            {children}
        </AuthContext.Provider>
    );
};

// Crie um hook personalizado para usar o contexto de autenticao facilmente
export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
};